import React, { useState, useEffect } from 'react';
import axiosInstance from "../Services/axiosApi";
import Loading from '../Components/Loading'
import getUser from '../Services/getUser'
import AddComponent from '../Components/AddComponent'
import HomeComponent from '../Components/HomeComponent'


function Home() {
	const [tricks, setTricks] = useState('');	
	const [load, setLoad] = useState(false);
	const [error, setError] = useState('');
	// const user = getUser()

	const capitaliseStyle = {
		textTransform: 'capitalize'
	}

	useEffect(() => {
		getAllTricks();		
	}, [])

	const getAllTricks = () => {
		const check = JSON.parse(localStorage.getItem("access_token"));
		axiosInstance.get('tricks/?format=json', { headers: {"Authorization" : `JWT ${check}`} })
			.then((response) => {
				const allTricks = response.data
				setTricks(allTricks)			
			})
			.catch(error => console.error(`Error: ${error}`));
	}

	if (tricks.length > 0) {
		return (
			<div>
				{tricks.map(trick => <div>{trick.name}</div>)}
			</div>	
		)
	} else {
		return (
			<div>
				loading
			</div>
		)	
	}
}

export default Home;

